/**
* Variables 
*/
html {
  overflow-x: hidden; }

body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400; }

.app {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  transition: transform 0.2s; }
  .app .app__page {
    width: calc(100% - 12rem);
    background: #fafafa;
    box-shadow: 0 12px 20px rgba(0, 0, 0, 0.2);
    position: relative;
    z-index: 1;
    transition: width 0.2s; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video, input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  box-sizing: border-box;
  vertical-align: baseline; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

a {
  text-decoration: none;
  outline: none; }

button {
  outline: none; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.sidebar {
  width: 12rem;
  background: #fafafa;
  border-right: 2px black solid;
  transition: width 0.2s;
  position: relative; }
  .sidebar .sidebar__section {
    display: flex;
    justify-content: center;
    padding: .5rem 0; }

@keyframes neon {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #228DFF, 0 0 70px #228DFF, 0 0 80px #228DFF, 0 0 100px #228DFF, 0 0 150px #228DFF; }
  to {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #228DFF, 0 0 35px #228DFF, 0 0 40px #228DFF, 0 0 50px #228DFF, 0 0 75px #228DFF; } }

.sidebar-logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  margin: 1rem 0 .5rem; }
  .sidebar-logo .sidebar-logo__text {
    text-align: center;
    font-size: 2.25rem; }
    .sidebar-logo .sidebar-logo__text a {
      color: #228DFF;
      transition: all 0.5s;
      font-family: Iceland, cursive; }
    .sidebar-logo .sidebar-logo__text a:hover {
      color: #ffffff;
      animation: neon 1.5s ease-in-out infinite alternate; }

.sidebar-user {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 1rem; }
  .sidebar-user .sidebar-user__avatar-wrapper {
    position: relative;
    width: 6rem;
    height: 6rem;
    overflow: hidden;
    border-radius: 55%;
    box-shadow: 0 0.75rem 1.25rem rgba(0, 0, 0, 0.2);
    transition: width 0.2s, height 0.2s; }
    .sidebar-user .sidebar-user__avatar-wrapper:hover .sidebar-user__overlay {
      opacity: 1; }
    .sidebar-user .sidebar-user__avatar-wrapper:hover .sidebar-user__icon {
      opacity: 1;
      transform: scale(1); }
  .sidebar-user .sidebar-user__avatar {
    width: 100%;
    height: 100%;
    object-fit: contain;
    transform: scale(2.3); }
  .sidebar-user .sidebar-user__overlay {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 0, 0, 0.5);
    border-radius: 50%;
    overflow: hidden;
    opacity: 0;
    transition: opacity 0.2s; }
  .sidebar-user .sidebar-user__icon {
    color: white;
    font-size: 2rem;
    transform: scale(5);
    opacity: 0;
    transition: opacity 1s, transform 0.2s; }
  .sidebar-user .sidebar-user__name-wrapper {
    display: flex;
    flex-direction: column;
    margin: .75rem; }
  .sidebar-user .sidebar-user__name {
    font-weight: 700;
    color: rgba(0, 0, 0, 0.54);
    transition: color 0.2s, font-size 0.2s; }
    .sidebar-user .sidebar-user__name:hover, .sidebar-user .sidebar-user__name:focus {
      color: #333333; }
  .sidebar-user .sidebar-user__status {
    font-size: 0.75rem;
    margin-top: .25rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: yellow;
    transition: font-size 0.2s; }
    .sidebar-user .sidebar-user__status:after {
      content: '';
      margin-left: .25rem;
      width: .4rem;
      height: .4rem;
      background: yellow;
      border-radius: 50%; }
    .sidebar-user .sidebar-user__status.sidebar-user__status--online {
      color: #8BC34A; }
      .sidebar-user .sidebar-user__status.sidebar-user__status--online:after {
        background: #8BC34A; }

@keyframes alert-pulse {
  20% {
    box-shadow: 0 0 0 0 rgba(255, 167, 38, 0.7); }
  80% {
    box-shadow: 0 0 0 0.75rem rgba(255, 167, 38, 0); } }

@keyframes alert-shake {
  5%, 15%, 25%, 35%, 45% {
    transform: translateX(-0.8rem); }
  10%, 20%, 30%, 40% {
    transform: translateX(0.8rem); }
  50% {
    transform: translateX(0); } }

.sidebar-nav {
  width: 100%; }
  .sidebar-nav .sidebar-nav__header {
    padding: .5rem 1rem;
    font-size: 0.75rem;
    font-weight: 700;
    color: #333333; }
  .sidebar-nav .sidebar-nav__item {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: 700;
    color: rgba(0, 0, 0, 0.54);
    font-size: 0.9rem;
    padding: 0 1rem;
    height: 2.5rem;
    position: relative;
    transition: width 0.2s, background 0.2s; }
    .sidebar-nav .sidebar-nav__item:hover, .sidebar-nav .sidebar-nav__item:focus {
      outline: none; }
      .sidebar-nav .sidebar-nav__item:hover .sidebar-nav__icon,
      .sidebar-nav .sidebar-nav__item:hover .sidebar-nav__title, .sidebar-nav .sidebar-nav__item:focus .sidebar-nav__icon,
      .sidebar-nav .sidebar-nav__item:focus .sidebar-nav__title {
        color: #a5c9ff; }
    .sidebar-nav .sidebar-nav__item.sidebar-nav__item--primary:before {
      content: '';
      width: 100%;
      height: 100%;
      background: #a5c9ff;
      position: absolute;
      z-index: 0;
      left: 100%;
      top: 0;
      opacity: 0; }
    .sidebar-nav .sidebar-nav__item.sidebar-nav__item--primary:hover:before, .sidebar-nav .sidebar-nav__item.sidebar-nav__item--primary:focus:before {
      left: 0;
      opacity: 1;
      width: .2rem;
      transition: left .1s, width .15s .1s, opacity 2s; }
    .sidebar-nav .sidebar-nav__item.sidebar-nav__item--secondary .sidebar-nav__icon-wrapper::after {
      content: 'arrow_forward';
      font-family: 'Material Icons';
      font-size: 1.25rem;
      color: rgba(0, 0, 0, 0.54);
      position: absolute;
      top: 0;
      left: 0;
      transform: translateX(100%);
      opacity: 0;
      transition-property: color, transform, opacity;
      transition-duration: 0.2s; }
    .sidebar-nav .sidebar-nav__item.sidebar-nav__item--secondary:hover .sidebar-nav__icon-wrapper::after, .sidebar-nav .sidebar-nav__item.sidebar-nav__item--secondary:focus .sidebar-nav__icon-wrapper::after {
      color: #ffb2b2;
      transform: translateX(0);
      opacity: 1; }
    .sidebar-nav .sidebar-nav__item.sidebar-nav__item--secondary:hover .sidebar-nav__icon, .sidebar-nav .sidebar-nav__item.sidebar-nav__item--secondary:focus .sidebar-nav__icon {
      color: #ffb2b2;
      transform: translateX(-100%); }
    .sidebar-nav .sidebar-nav__item.sidebar-nav__item--secondary:hover .sidebar-nav__title, .sidebar-nav .sidebar-nav__item.sidebar-nav__item--secondary:focus .sidebar-nav__title {
      color: #ffb2b2; }
    .sidebar-nav .sidebar-nav__item.sidebar-nav__item--secondary .sidebar-nav__notification {
      background: linear-gradient(45deg, green 0, #00cd00 75%, green 100%);
      box-shadow: 0 0.75rem 1.25rem rgba(0, 128, 0, 0.2); }
    .sidebar-nav .sidebar-nav__item.sidebar-nav__item--secondary.sidebar-nav__item--alert .sidebar-nav__title {
      color: orange; }
    .sidebar-nav .sidebar-nav__item.sidebar-nav__item--secondary.sidebar-nav__item--alert .sidebar-nav__icon {
      color: orange;
      animation: alert-pulse 2s infinite, alert-shake 2s infinite;
      border-radius: 50%; }
  .sidebar-nav .sidebar-nav__icon-wrapper {
    position: relative;
    margin-right: 1rem;
    width: 1.5rem; }
  .sidebar-nav .material-icons {
    font-size: 1.5rem;
    color: rgba(0, 0, 0, 0.54);
    transition-property: color, transform, opacity;
    transition-duration: 0.2s; }
  .sidebar-nav .sidebar-nav__title {
    transition: color 0.2s; }
  .sidebar-nav .sidebar-nav__notification {
    position: absolute;
    right: 1rem;
    font-size: 0.625rem;
    font-weight: 400;
    color: pink;
    background: linear-gradient(45deg, #333333 0, #5e5e5e 50%, #333333 100%);
    padding: .25rem .5rem;
    border-radius: 0.625rem;
    box-shadow: 0 0.75rem 1.25rem rgba(51, 51, 51, 0.2); }

.sidebar-toggler__checkbox {
  position: absolute;
  z-index: -10; }
  .sidebar-toggler__checkbox:checked + .sidebar {
    width: 4.6rem; }
    .sidebar-toggler__checkbox:checked + .sidebar .sidebar-toggler {
      width: 100%;
      height: 3rem; }
    .sidebar-toggler__checkbox:checked + .sidebar .sidebar-toggler__icon::before {
      transform: rotate(63deg); }
    .sidebar-toggler__checkbox:checked + .sidebar .sidebar-toggler__icon::after {
      transform: rotate(-63deg); }
    .sidebar-toggler__checkbox:checked + .sidebar .sidebar-logo__wrapper {
      height: 0;
      overflow: hidden; }
    .sidebar-toggler__checkbox:checked + .sidebar .sidebar-sure {
      padding: 0 .25rem; }
    .sidebar-toggler__checkbox:checked + .sidebar .sidebar-user__avatar-wrapper {
      width: 3rem;
      height: 3rem; }
    .sidebar-toggler__checkbox:checked + .sidebar .sidebar-user__name, .sidebar-toggler__checkbox:checked + .sidebar .sidebar-user__status {
      font-size: 0.625rem; }
    .sidebar-toggler__checkbox:checked + .sidebar .sidebar-nav__header::before {
      content: attr(data-toggled-abbr);
      text-transform: uppercase;
      margin-right: 2rem; }
    .sidebar-toggler__checkbox:checked + .sidebar .sidebar-nav__item {
      z-index: 2;
      overflow: hidden;
      width: 3.5rem; }
      .sidebar-toggler__checkbox:checked + .sidebar .sidebar-nav__item:hover, .sidebar-toggler__checkbox:checked + .sidebar .sidebar-nav__item:focus {
        background: #fafafa;
        width: 10rem;
        box-shadow: 0 0.25rem 1rem rgba(0, 0, 0, 0.15); }
    .sidebar-toggler__checkbox:checked + .sidebar .sidebar-nav__notification {
      right: .25rem;
      top: .25rem;
      padding: .1rem .25rem;
      font-size: .5rem; }
  .sidebar-toggler__checkbox:checked ~ .app__page {
    width: calc(100% - 3.5rem); }

.sidebar-toggler {
  position: absolute;
  z-index: 2;
  right: 0;
  top: 0;
  width: 2rem;
  height: 2rem;
  cursor: pointer; }
  .sidebar-toggler:hover .sidebar-toggler__icon::before, .sidebar-toggler:hover .sidebar-toggler__icon::after {
    background: blue; }
  .sidebar-toggler .sidebar-toggler__icon {
    width: .75rem;
    height: .75rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
    .sidebar-toggler .sidebar-toggler__icon::before, .sidebar-toggler .sidebar-toggler__icon::after {
      content: '';
      position: absolute;
      width: .75rem;
      height: .12rem;
      background: black;
      transition: transform 0.4s, background 0.2s; }
    .sidebar-toggler .sidebar-toggler__icon::before {
      top: 0;
      transform: rotate(-63deg); }
    .sidebar-toggler .sidebar-toggler__icon::after {
      bottom: 0;
      transform: rotate(63deg); }

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: red;
  height: 3.5rem;
  background: #fff;
  box-shadow: 0 0.25rem 1rem rgba(0, 0, 0, 0.15);
  padding: 0 1rem; }
  .navbar .navbar__wrapper {
    display: flex;
    flex-direction: row;
    height: 100%; }
  .navbar .navbar__section {
    display: flex;
    align-items: center;
    margin-left: 1rem;
    height: 100%; }

.navbar-search__input {
  position: absolute;
  z-index: 1;
  opacity: 0;
  left: 50%;
  top: 1.5rem;
  transform: translate(-50%, -50%);
  font-size: 1.5rem;
  padding: 0 2rem;
  width: 4rem;
  height: 4rem;
  border: 0.1rem solid #333333;
  border-radius: 2rem;
  transition-property: width, top, opacity;
  transition-duration: 0.2s; }
  .navbar-search__input:focus {
    width: calc(100% - 26rem);
    top: 5rem;
    opacity: 1;
    outline: none; }
    .navbar-search__input:focus + .app {
      transform: perspective(70rem) rotateX(30deg); }

.navbar-search {
  width: 3rem;
  height: 3rem;
  position: relative; }
  .navbar-search .navbar-search__label-icon {
    display: block;
    width: inherit;
    height: inherit;
    cursor: pointer;
    padding: 1.5rem;
    overflow: hidden; }
    .navbar-search .navbar-search__label-icon:hover .navbar-search__icon {
      color: #333333;
      text-shadow: 0 0.75rem 1.25rem rgba(0, 0, 0, 0.2);
      font-size: calc(2rem + 50%); }
    .navbar-search .navbar-search__label-icon:hover .navbar-search__name {
      color: #333333;
      text-shadow: 0 0.75rem 1.25rem rgba(0, 0, 0, 0.2);
      left: 2.5rem;
      opacity: 1; }
  .navbar-search .navbar-search__icon {
    color: rgba(0, 0, 0, 0.54);
    font-size: 2rem;
    line-height: 1.25;
    position: relative;
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%);
    transition-property: color, text-shadow, font-size;
    transition-duration: 0.2s; }
  .navbar-search .navbar-search__name {
    color: rgba(0, 0, 0, 0.54);
    line-height: 1.25;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0;
    transition-property: color, text-shadow, opacity, left;
    transition-duration: 0.2s; }
